.pipeline-icon {
  opacity: 0.7;
  transition: opacity 300ms ease; }
  .kui-theme--light .pipeline-icon {
    fill: #000; }
  .kui-theme--dark .pipeline-icon {
    fill: #fff; }

.kui-theme--light .pipeline-icon--stroke {
  fill: none;
  stroke: #000; }

.kui-theme--dark .pipeline-icon--stroke {
  fill: none;
  stroke: #fff; }
